.active {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    100deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(2, 26, 52, 1) 100%
  );
}

.hamburger {
  cursor: pointer;
  width: 24px;
  height: 24px;
  transition: all 0.25s;
  position: relative;
}

.hamburger-top,
.hamburger-middle,
.hamburger-bottom {
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background: #fff !important;
  transform: rotate(0);
  transition: all 0.5s;
}

.hamburger-middle {
  transform: translateY(7px);
}

.hamburger-bottom {
  transform: translateY(14px);
}

.open {
  transform: rotate(90deg);
  transform: translateY(0px);
}

.open .hamburger-top {
  transform: rotate(45deg) translateY(6px) translate(6px);
}

.open .hamburger-middle {
  display: none;
}

.open .hamburger-bottom {
  transform: rotate(-45deg) translateY(6px) translate(-6px);
}
.hero-animation {
  transition: all 0.5s ease-out hero-animation forwards;
}

.mobileNavOpen {
  display: flex;
  animation: navOpen 0.5s forwards;
}

.mobileNavClose {
  display: flex;
  animation: navClose 0.5s forwards;
}

@keyframes navClose {
  from {
    transform: translateX(0%);
  }
  to {
    transform: translateX(100%);
    display: none;
  }
}

@keyframes navOpen {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0%);
  }
}

@keyframes hero-animation {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
